p,span{font-family:'IBM Plex Mono', monospace}.font-Permanent-Marker{font-family:'Permanent Marker', cursive;font-size:25px;font-weight:bold;color:black}.page-header{padding:30px 0 0 0}.page-header .top-nav{position:relative;display:flex;justify-content:space-between;padding-bottom:10px}.page-header .top-nav:after{content:'';position:absolute;z-index:21;width:100%;height:10px;left:0;bottom:8px;background-repeat:no-repeat;background-image:url(data:image/png;base64,)}.page-header .top-nav .menus{display:flex;align-items:center}.page-header .top-nav .navbar-right{padding-right:10px;display:flex;align-items:center}.page-header .top-logo{margin-top:30px}.page-header .top-logo img{max-width:100%}.backdrop{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.3);z-index:100}@media screen and (max-width: 760px){.page-header .top-nav{padding-bottom:20px;align-items:center}.navbar-left.menus{display:none !important}.navbar-right .twitter-icons,.navbar-right span{display:none !important}}@media screen and (min-width: 759px){.toggle-btn{display:none}.toolbar_logo{margin-left:0}}.side-drawer{font-family:'Permanent Marker', cursive;height:100%;color:black;background:white;box-shadow:2px 0px 5px rgba(0,0,0,0.5);position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:200;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:-webkit-transform 0.3s ease-out;transition:transform 0.3s ease-out;transition:transform 0.3s ease-out, -webkit-transform 0.3s ease-out}.side-drawer.open{-webkit-transform:translateX(0);transform:translateX(0)}.side-drawer ul{height:100%;list-style:none;display:flex;flex-direction:column;justify-content:center}.side-drawer ul li{margin:0.5rem 0;display:flex;align-items:center}.side-drawer ul li a{text-decoration:none;color:black;font-size:1.2rem}@media screen and (min-width: 759px){.side-drawer{display:none}}.page-footer{padding:30px 0 0 0}.page-footer .footer-nav{position:relative;display:flex;justify-content:space-between;padding-bottom:10px}.page-footer .footer-nav:after{content:'';position:absolute;width:100%;height:10px;left:0;bottom:8px;background-repeat:no-repeat;background-image:url(data:image/png;base64,);z-index:21}.page-footer .footer-nav .menus{display:flex;align-items:center}.page-footer .footer-nav .navbar-right{padding-right:10px;display:flex;align-items:center}.page-footer .footer-logo{padding:20px 0}.page-footer .footer-logo .container-fluid{display:flex;justify-content:space-between;align-items:flex-end}.page-footer .footer-logo .container-fluid img{max-width:100%}@media screen and (max-width: 760px){.footer-logo{padding:20px 0}.footer-logo .container-fluid{flex-direction:column-reverse !important;align-items:center !important}.footer-logo .container-fluid img{margin-top:10px}}.toggle-btn{padding:0 20px;margin-top:-10px}.toggle-button{height:24px;width:30px;background:transparent;border:none;cursor:pointer;display:flex;flex-direction:column;justify-content:space-around;padding:0;box-sizing:border-box}.toggle-button:focus{outline:none}.toggle-button-line{width:30px;height:2px;background:black}@media screen and (max-width: 760px){.faq-wrapper{flex-wrap:wrap}.faq-wrapper img{margin:0 auto !important}.faq-wrapper p{margin:1rem 0 0 0 !important}}

